@tailwind base;

@tailwind components;

@tailwind utilities;

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: Poppins;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.custom-body {
  width: calc(100% - 300px);
  left: 300px;
}

.custom-left {
  left: -3.3rem;
}

.brand-hover:hover {
  color: #fff !important;
}

@media (max-width: 800px) {
  .toggle .w-custom-width {
    transition: all .5s ease-in-out;
    width: 73% !important;
    left: 0 !important;
  }

  .sidebar .w-custom-width {
    transition: all .5s ease-in-out;
    z-index: 100 !important;
    width: 73% !important;
    height: 100vh !important;
    position: fixed !important;
    top: 0 !important;
    left: -100% !important;
  }

  .custom-body {
    width: 100%;
    left: 0;
  }

  .close-menu, .open-menu, .toggle .close-menu, .sidebar .open-menu {
    display: block !important;
  }

  .toggle .open-menu, .sidebar .close-menu {
    display: none !important;
  }
}

.close-menu, .open-menu {
  cursor: pointer;
  z-index: 1;
  display: none;
  position: fixed;
}

.open-menu {
  font-size: 35px;
  top: 50px;
  left: 40px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.close-menu {
  font-size: 35px;
  top: 20px;
  right: 20px;
}

.toggle .custom-body {
  background-color: rgba(0, 0, 0, .4);
  overflow: hidden;
}

.bg-img {
  background-image: url("home-bg1.a3a73355.jpg");
  background-position: top;
  background-size: cover;
}

.bg-theme {
  background-color: #a3c0bc;
}

#Home .flex {
  display: flex !important;
}

.slick-prev, .slick-next {
  display: none !important;
}

.slick-dots {
  bottom: 60px !important;
}

.tech .bg-theme {
  background: linear-gradient(to right, #445061 50%, #a3c0bc 50%) 100% 100% / 200% 100%;
  transition: all .5s ease-out;
}

.tech .bg-theme:hover {
  background-position: 0 100%;
}

.tech .leading-tight {
  visibility: hidden;
  text-align: center;
  transition: .5 sec ease-in;
  position: absolute;
  left: 0;
}

.tech .bg-theme:hover .leading-tight {
  visibility: visible;
  transition: .5 sec ease-in;
  color: #fff;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-weight: normal;
  display: flex;
  top: 0;
}

